</template>
<script>


import axios from "axios";
export default {
    name: "AndharBaharView",
    props: ['rowsrec'],
    data() {
        return {
            startnum: null,
            isActive: false,
            userdetails: {
                username: null,
                balance: null,
                period: null,
                slide: 48,

            },
            image: {
                i1: require('@/assets/includes/ab/1.png'),
                i2: require('@/assets/includes/ab/2.png'),
                i3: require('@/assets/includes/ab/3.png'),
                i4: require('@/assets/includes/ab/4.png'),
                i5: require('@/assets/includes/ab/5.png'),
                i6: require('@/assets/includes/ab/6.png'),
                i7: require('@/assets/includes/ab/7.png'),
                i8: require('@/assets/includes/ab/8.png'),
                i9: require('@/assets/includes/ab/9.png'),
                andar: require('@/assets/includes/ab/and_bk.png'),
                middle: require('@/assets/includes/ab/avbcards_bk.png'),
                bahar: require('@/assets/includes/ab/bnd_bk.png'),
            },
            betbox: {
                ani:null,
                title: null,
                target: null,
                boxcolor: null,
                timeout: true,
                multiply: 1,
                factor: 10,
                amount: 10,
                page: 1,
                page1: 1,
                total: 1000,
                total1: null,

            },
            resultrec: '',
            info: '',
            betrec: ''
        }

    },
    mounted: function () {
        this.countdown();
        this.refreshcountdown = setInterval(this.countdown, 1000);
        if (this.startnum != null) {
            this.st();
        } else {
            this.m= setInterval(this.st, 1000);
        }
    },
    beforeUnmount: function () {
        console.log("unmount");
        clearInterval(this.refreshcountdown);

    },
    created: function () {
        this.fechresultrec();
       
        // this.expand();
    },
    methods: {
        moveup() {
            document.getElementById("upimg" + this.startnum).style.marginTop = "0px";
            console.log("start");
        },
        st() {
            if(this.startnum != null){
                clearInterval(this.m);
                document.getElementById("upimg" + this.startnum).style.display = "";
            this.move = setTimeout(this.moveup, 1000);
            }else{
                document.getElementById("upimg" + this.startnum).style.display = "";
            this.move = setTimeout(this.moveup, 1000);
            }
            
        },

        getresclass(res) {
            if (res == 'A') {
                return 'GS A';
            } else if (res == 'B') {
                return 'RS B';
            } else if (res == 'T') {
                return 'VS T';
            }
        },
        pop(message) {
            document.getElementById("message").innerHTML = message;
            document.getElementById("clink").style.display = "";

        }, cpop() {
            document.getElementById("clink").style.display = "none";

        },
        fechresultrec() {

            axios.get('https://jhargames.com/9987/src/api/bet.php?action=result&server=AndharBahar').then((res) => {
                this.resultrec = res.data;
            }).catch((err) => {
                console.log(err);
            })
            this.userdetails.username = localStorage.getItem('username');
            axios.get('https://jhargames.com/9987/src/api/bet.php?action=betrec&user=' + this.userdetails.username + '&server=AndharBahar').then((res) => {
                this.betrec = res.data;

            }).catch((err) => {
                console.log(err);
            })

            axios.get('https://jhargames.com/9987/src/api/bet.php?action=info&user=' + this.userdetails.username + '&per=AndharBahar').then((res) => {
                this.info = res.data;
                this.betbox.total = this.info[1].total;
                this.betbox.total1 = this.info[3].total1;
                this.userdetails.balance = this.info[0].balance;
                this.userdetails.period = this.info[2].period;
                this.startnum = this.info[4].startnum;

            }).catch((err) => {
                console.log(err);
            })
        },
        reset(){
            document.getElementById("a1").style.display="none";
            document.getElementById("a2").style.display="none";
            document.getElementById("a3").style.display="none";
            document.getElementById("a4").style.display="none";
            document.getElementById("a5").style.display="none";
            document.getElementById("a6").style.display="none";
            document.getElementById("a7").style.display="none";
            document.getElementById("a8").style.display="none";
            document.getElementById("a9").style.display="none";

            document.getElementById("m1").style.display="none";
            document.getElementById("m2").style.display="none"; 
            document.getElementById("m3").style.display="none"; 
            document.getElementById("m4").style.display="none"; 
            document.getElementById("m5").style.display="none"; 
            document.getElementById("m6").style.display="none"; 
            document.getElementById("m7").style.display="none"; 
            document.getElementById("m8").style.display="none"; 
            document.getElementById("m9").style.display="none";  
            


            document.getElementById("b1").style.display="none";
            document.getElementById("b2").style.display="none";
            document.getElementById("b3").style.display="none";
            document.getElementById("b4").style.display="none";
            document.getElementById("b5").style.display="none";
            document.getElementById("b6").style.display="none";
            document.getElementById("b7").style.display="none";
            document.getElementById("b8").style.display="none";
            document.getElementById("b9").style.display="none";
        },
        betamount(x, z, a) {
            var amount;
            var f;
            if (z == "fail") {
                f = 95 / 100 * x;
                amount = "<r> -" + (f.toFixed(2)) + "</r>";
                return amount;
            } else if (z == 'success') {
                if (a == "A" || a=="B") {
                        f = 95 / 100 * x * 2;
                        amount = "<g>+" + f.toFixed(2) + "</g>";
                        return amount;
                    } else {
                        f = 95 / 100 * x * 9;
                        amount = "<g>+v" + f.toFixed(2) + "</g>";
                        return amount;
                    }
                

            } else {
                return "<span class='icon wait'></span>";
            }
        },
        readrule() {

            document.getElementById("xrule").style.display = "";
        },
        closerule() {
            document.getElementById("xrule").style.display = "none";
        },
        join(label) {
            console.log(label);
            if (this.betbox.timeout) {
                if (label == "Andhar") {
                    this.userdetails.slide = "A";
                    this.betbox.boxcolor = "#6298e8";
                    this.betbox.title = "Andar";
                } else if (label == "Bahar") {
                    this.userdetails.slide = "B";
                    this.betbox.boxcolor = "red";
                    this.betbox.title = "Bahar";
                } else if (label == "Tie") {
                    this.userdetails.slide = "T";
                    this.betbox.boxcolor = "tie";
                    this.betbox.title = "Tie";
                }

                document.getElementById("nfrm").style.display = "block";
                document.getElementById("blackscreen").style.display = "block";
                this.fechresultrec();
            }

        },
        closescreen() {
            document.getElementById("nfrm").style.display = "none";
            document.getElementById("blackscreen").style.display = "none";
        },
        betclass(name) {
            return name + ' ' + this.betbox.boxcolor;
        },

        closebetbox() {
            document.getElementById("betbox").style.display = "none";
            var listItems = document.getElementById('factorlist').childNodes;
            // Remove 'active' tag for all list items
            for (let i = 0; i < listItems.length; i++) {
                listItems[i].classList.remove('active');
            }
            this.betbox.amount = this.betbox.factor * this.betbox.multiply
            document.getElementById("10").className = "active";
            this.betbox.multiply = 1;
            this.betbox.factor = 10;
            this.betbox.amount = 10;
        },
        selectmultiply(multi) {
            var listItems = document.getElementById('factorlist').childNodes;
            // Remove 'active' tag for all list items
            for (let i = 0; i < listItems.length; i++) {
                listItems[i].classList.remove('selected');
            }
            this.betbox.factor = multi;
            this.betbox.amount = this.betbox.factor * this.betbox.multiply
            document.getElementById(multi).className = "cont-amt selected " + this.betbox.target;
        },
        plus(value) {
            this.betbox.multiply = this.betbox.multiply + value;
            this.betbox.amount = this.betbox.factor * this.betbox.multiply
        },
        minus(value) {
            if (this.betbox.multiply > value) {
                this.betbox.multiply = this.betbox.multiply - value;
                this.betbox.amount = this.betbox.factor * this.betbox.multiply
            }

        },
        myorder() {
            document.getElementById("every").classList.remove("active");
            document.getElementById("my").classList.add("active");
            document.getElementById("myorder").style.display = "";
            document.getElementById("everyoneorder").style.display = "none";
        },
        everyoneorder() {
            document.getElementById("my").classList.remove("active");
            document.getElementById("every").classList.add("active");
            document.getElementById("myorder").style.display = "none";
            document.getElementById("everyoneorder").style.display = "";
        },
        countdown() {
            var countDownDate = Date.parse(new Date) / 1e3;
            //var now = new Date().getTime();
            var distance = 60 - countDownDate % 60;
            //alert(distance);
            var i = distance / 60,
                n = distance % 60;
            //o = n / 10,
            // s = n % 10;
            var minutes = Math.floor(i);
            var seconds = ('0' + Math.floor(n)).slice(-2);
            var sec1 = (seconds % 100 - seconds % 10) / 10;
            var sec2 = seconds % 10;
            document.getElementById("demo").innerHTML = "<span class='cdspn'>0</span><span class='cdspn' id='fsm'> " + Math.floor(minutes) + "</span>:<span class='cdspn' id='fs0'>" + sec1 + "</span><span class='cdspn' id='fs1'>" + sec2 + "</span>";
            
            if (11 > distance) {
                document.getElementById("jaba").classList.add("disabled");
                document.getElementById("jabb").classList.add("disabled");
                document.getElementById("jabt").classList.add("disabled");

                document.getElementById("everyone").stop();
                document.getElementById("blackscreen").style.display = "none";
                this.betbox.timeout = false;
            }
            if (60 == distance) {
                document.getElementById("demo").style.display="none";
                document.getElementById("ndemo").style.display="";
            }
            if (59 == distance) {
                axios.get('https://jhargames.com/9987/src/api/bet.php?action=result&server=AndharBahar').then((res) => {
                    this.ani = res.data[res.data.length-1].clo;
                    console.log(this.ani);
                    this.animate(this.ani);
            }).catch((err) => {
                console.log(err);
            })
            }
          
           
            

        },
        toggle(idx) {
            if (document.getElementById(idx).style.display == "") {
                document.getElementById(idx).style.display = "none";
            } else {
                document.getElementById(idx).style.display = "";
            }
        },
        confirmbet() {
            document.getElementById("nfrm").style.display = "none";
            document.getElementById("blackscreen").style.display = "none";
            if (this.userdetails.balance >= this.betbox.amount) {
                var data = new FormData();
                data.append("username", this.userdetails.username);
                data.append("period", this.userdetails.period);
                data.append("amount", this.betbox.amount);
                data.append("ans", this.userdetails.slide);
                axios
                    .post(
                        "https://jhargames.com/9987/src/api/bet.php?action=bet&server=AndharBahar",
                        data
                    )
                    .then(res => {
                        console.log(res.data);
                        this.closebetbox();
                        this.fechresultrec();
                    })
                    .catch(err => {
                        console.log("Error", err);
                    });
                this.fechresultrec();
            } else {
                this.pop("Low Balance!");
            }

        },
        load() {
            this.fechresultrec();
        },
        moveandar(num) {
            document.getElementById("m" + num).style.display = "";
            document.getElementById("m" + num).style.marginTop = "30px";
            //document.getElementById("m"+num).style.right = "211px";


            let id = null;
            const elem = document.getElementById("m" + num);
            let pos = 0;
            clearInterval(id);
            id = setInterval(frame, 1);
            function frame() {
                if (pos == 210) {
                    clearInterval(id);
                    document.getElementById("a" + num).display = "";
                } else {
                    pos += 5;
                    elem.style.right = pos + 'px';
                }
            }
        },
        movebahar(num) {
            document.getElementById("m" + num).style.display = "";
            document.getElementById("m" + num).style.marginTop = "30px";
            //document.getElementById("m"+num).style.right = "211px";


            let id = null;
            const elem = document.getElementById("m" + num);
            let pos = 0;
            clearInterval(id);
            id = setInterval(frame, 1);
            function frame() {
                if (pos == 210) {
                    clearInterval(id);
                    document.getElementById("b" + num).display = "";
                } else {
                    pos += 5;
                    elem.style.left = pos + 'px';
                }
            }
        },

        getele(ele) {
            if (ele == 'A') {
               
                    return "<div class='GS A'><div class='tpr'>" + ele + "</div></div>"
               

            } else if (ele== 'B') {
                
                    return "<div class='RS B'><div class='tpr'>" + ele + "</div></div>"
                
            } else if (ele== 'T') {
             
                    return "<div class='VS T'><div class='tpr'>" + ele + "</div></div>"
               
            }else {
                return "<div class='' style='margin-left: 25%;'><span class='icon wait'></span></div>"
            }
        },
        short(stat) {
            if (stat == "red") {
                return 'R';
            } else if (stat == "green") {
                return 'G';
            } else if (stat == "violet") {
                return 'V';
            } else {
                return stat;
            }
        },
        animate(statement) {
            this.result = statement
            this.id = null;
            this.fps = 0;
            this.id = setInterval(this.play, 1000);

        },
        play() {
            console.log(this.result, this.fps, this.result.length)
            if (this.fps == this.result.length) {
                clearInterval(this.id);
                 this.betbox.timeout = true;
                document.getElementById("demo").style.display="";
                document.getElementById("ndemo").style.display="none";
                document.getElementById("jaba").classList.remove("disabled");
                document.getElementById("jabb").classList.remove("disabled");
                document.getElementById("jabt").classList.remove("disabled");
               
                this.fechresultrec();
                document.getElementById("everyone").start();
                this.reset();
                this.st = setTimeout(this.st, 1000);
            } else {
                if (this.fps % 2 == 0) {
                    console.log(this.result[this.fps]);
                    this.moveandar(this.result[this.fps]);
                } else {
                    console.log(this.result[this.fps]);
                    this.movebahar(this.result[this.fps]);
                }
                this.fps = this.fps + 1;
            }
        }



    }
}

</script>